/**
 * 首页 - 我在做些什么？！
 * @module /views/Index/index
 */

import './index.scss'
import React from 'react'
import { List, Icon, InputItem } from 'antd-mobile'
import Record from '../../models/Record'
import CAction from '../../components/CAction'
import { Local } from '../../uitls/Store'

const Item = List.Item

export const LocalKey = 'waid-index-record-list'

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      recordList: this.loadeRecordList(),
      pressingItemIndex: null
    }
    this.timerID = null
  }

  componentDidMount() {
    this.start()
  }

  componentDidUpdate(prevProps, prevState) {
    this.storeRecordList()
  }

  componentWillUnmount() {
    this.stop()
  }

  start(index) {
    let recordList = this.state.recordList
    let record = recordList[index] || new Record()
    if (record.startTime) {
      record.stop()
    } else {
      record.start()
    }
    // 开始事项置顶
    if (index >= 0 && index < recordList.length && record.startTime) {
      recordList.splice(index, 1)
      recordList.unshift(record)
    }
    let _activeRecordList = []
    for (let i = recordList.length - 1; i >= 0; i--) {
      let _record = recordList[i]
      if (_record.startTime) {
        recordList.splice(i, 1)
        _activeRecordList.push(_record)
      }
    }
    _activeRecordList.forEach(el => {
      recordList.unshift(el)
    })
    let started = false
    recordList.forEach(el => {
      started = started || el.startTime
    })
    if (!started) {
      this.setState({
        recordList: recordList
      })
      this.stop()
    } else if (!this.timerID) {
      this.timerID = setInterval(() => {
        recordList.forEach(el => {
          if (el.startTime) el.updateRelativeStr()
        })
        this.setState({
          recordList: recordList
        })
      }, 1000)
    }
  }

  stop() {
    clearInterval(this.timerID)
    this.timerID = null
    this.storeRecordList()
  }

  loadeRecordList() {
    let tmp = Local.get(LocalKey) || []
    let records = []
    tmp.forEach(el => {
      let record = new Record(el)
      record.visible = true
      records.push(record)
    })
    return records
  }

  storeRecordList() {
    Local.set(LocalKey, this.state.recordList)
  }

  addItem() {
    let value = ''
    while (!value && value !== null) {
      value = window.prompt('输入事项名称', this.state.recordList.length > 0 ? '' : '早餐')
      if (!value && value !== null) alert('请输入正确的事项名称')
    }
    if (value) {
      let recordList = this.state.recordList
      recordList.push(new Record(value))
      this.setState({
        recordList: recordList
      })
    }
  }

  setPressingItem(item) {
    this.setState({
      pressingItemIndex: item
    })
  }

  filterListByKeyWord(keyWord) {
    let recordList = this.state.recordList
    recordList.forEach(item => {
      item.visible = item.title.indexOf(keyWord) >= 0 || !keyWord
    })
    this.setState({
      recordList: recordList
    })
  }

  render() {
    return (
      <section className="index">
        <div className="index-header">
          <div className="index-header-title">我在做些什么？!</div>
          <InputItem
            clear
            placeholder="输入关键字进行搜索"
            onChange={value => {
              this.filterListByKeyWord(value)
            }}
          />
        </div>
        {/* 事项列表 */}
        <List className="index-list">
          {this.state.recordList.map((item, index) =>
            item.visible ? (
              <CAction
                onLongPress={() => {
                  this.start(index)
                  this.setPressingItem()
                }}
                onTouchStart={() => {
                  this.setPressingItem(index)
                }}
                onTouchMove={() => {
                  this.setPressingItem()
                }}
                onTouchEnd={() => {
                  this.setPressingItem()
                }}
                key={index}
              >
                <Item extra={item.relativeStr} className={[item.startTime && 'index-list-item-active', this.state.pressingItemIndex === index && 'index-list-item-pressing']}>
                  <span className="index-list-item-title">{item.title}</span>
                </Item>
              </CAction>
            ) : (
              ''
            )
          )}
        </List>
        {/* 添加按钮 */}
        <div className="index-add float-btn" onClick={() => this.addItem()}>
          <Icon type="cross" size="lg" color="#666" />
        </div>
      </section>
    )
  }
}

export default Index
