/*
 * 数据备份与恢复
 * @module /views/MigrateAndRecover/index
 * @since 2019-05-18 14:35:00
 */
import './index.scss'
import { Tabs, TextareaItem, Button } from 'antd-mobile'
import React from 'react'
import { LocalKey as IndexRecordListLocalKey } from '../Index/index'
import { Local } from '../../uitls/Store'
import md5 from 'blueimp-md5'
import Clipboard from 'react-clipboard.js'

const LocalKeys = [IndexRecordListLocalKey]

const tabs = [{ title: '数据备份' }, { title: '数据恢复' }]

const migrate = {
  value: '',
  doing: false
}

const recover = {
  value: '',
  doing: false
}

class MigrateAndRecover extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      migrate,
      recover
    }
  }

  componentDidMount() {
    this.loadMigrateData()
  }

  loadMigrateData() {
    let localValueObj = ''
    LocalKeys.forEach(el => {
      let val = Local.get(el)
      if (val !== '' && val !== null) {
        localValueObj = localValueObj || {}
        localValueObj[el] = Local.get(el)
      }
    })
    if (localValueObj) {
      migrate.value = btoa(encodeURIComponent(JSON.stringify(localValueObj)))
      migrate.value = migrate.value + '|' + md5(migrate.value)
      this.setState({
        migrate: migrate
      })
    }
  }

  handleInputChange(value) {
    recover.value = value
    this.setState({
      recover
    })
  }

  recoverData() {
    if (recover.value) {
      let valArr = recover.value.split('|')
      let objVal = valArr[0]
      let md5Val = valArr[1]
      let pass = true
      if (md5(objVal) !== md5Val) pass = false
      let localValueObj = {}
      try {
        localValueObj = JSON.parse(decodeURIComponent(atob(objVal)))
      } catch (err) {
        pass = false
      }
      if (pass) {
        if (window.confirm('准备完成，是否进行恢复？')) {
          for (let key in localValueObj) {
            Local.set(key, localValueObj[key])
          }
          migrate.value = recover.value
          this.setState({
            migrate
          })
          alert('数据恢复完成！')
        }
      } else {
        alert('应用数据检查失败，请重新粘贴！')
        recover.value = ''
        this.setState({
          recover
        })
      }
    }
  }

  render() {
    return (
      <section className="migrateandrecover">
        <Tabs
          tabs={tabs}
          initalPage={'t2'}
          swipeable={false}
          renderTabBar={props => {
            return (
              <div className="migrateandrecover-tab-box">
                <Tabs.DefaultTabBar {...props} />
              </div>
            )
          }}
        >
          <div className="migrateandrecover-migrate-box">
            <TextareaItem value={this.state.migrate.value} rows={10} placeholder="暂无待备份应用数据" />
            <div className="btn-box">
              <Clipboard
                className="migrateandrecover-migrate-copy-btn"
                data-clipboard-text={this.state.migrate.value}
                onSuccess={() => {
                  alert('复制成功！')
                }}
                onError={() => {
                  alert('复制失败，请手动选择复制')
                }}
              >
                <Button type="ghost">复制应用数据</Button>
              </Clipboard>
            </div>
            <div className="migrateandrecover-hint-box">
              <p className="migrateandrecover-hint-title">操作提示：</p>
              <p>1、数据备份用于导出、备份应用数据到其他浏览器、设备；</p>
              <p>2、点击“复制应用数据”按钮复制应用数据，若复制失败，则点击文本数据框，长按选中复制，切勿编辑文本；</p>
              <p>3、为保护个人隐私，应用数据请妥善传输、存储；</p>
              <p>4、为确保应用数据不丢失，应先在其他浏览器、设备打开应用恢复完成后，再清除当前应用数据。</p>
            </div>
          </div>
          <div>
            <TextareaItem
              onChange={value => {
                this.handleInputChange(value)
              }}
              value={this.state.recover.value}
              rows={10}
              placeholder="粘贴待恢复应用数据到此处"
            />
            <div className="btn-box">
              <Button
                onClick={() => {
                  this.recoverData()
                }}
                loading={this.state.recover.doing}
                type="ghost"
              >
                恢复应用数据
              </Button>
            </div>
            <div className="migrateandrecover-hint-box">
              <p className="migrateandrecover-hint-title">操作提示：</p>
              <p>1、数据恢复用于恢复从其他设备、浏览器导出的应用数据；</p>
              <p>2、点击文本输入框，粘贴应用数据到输入框中；</p>
              <p>3、点击“恢复应用数据”按钮进行恢复。</p>
            </div>
          </div>
        </Tabs>
      </section>
    )
  }
}

export default MigrateAndRecover
