/**
 * 文档标题操作
 */

// 导入 ios 环境判断方法
import { ios } from './environment'

/**
 * 设置页面标题
 * @param {*} title
 * * @example
 * import { set as setTitle } from 'knife/utils/title'
 * setTitle('首页')
 */
export function set(title) {
  document.title = title
  // hack 在 IOS webview 中修改 document.title 页头显示并没有改变的情况
  if (!ios()) return
  var iframe = document.createElement('iframe')
  iframe.src = ''
  iframe.style.display = 'none'
  iframe.onload = function() {
    var tid = setTimeout(function() {
      iframe.remove()
      clearTimeout(tid)
    }, 0)
  }
  document.body.appendChild(iframe)
}

// 导出默认模块
export default {
  set
}
