/**
 * 动作捕捉组件
 * @module /components/CAction
 * @example
 * // handlerDoubleTap
 * render() {
 *   return <CAction onDoubleTap={() => { this.handlerDoubleTap() }}>handlerDoubleTap</CAction>
 * }
 */
import React from 'react'

export default class CAction extends React.Component {
  constructor() {
    super()
    this._longPressTimerId = null
    this._longPressTime = 500
  }

  componentDidMount() {
    this._onDoubleTap = typeof this.props.onDoubleTap === 'function' ? this.props.onDoubleTap : function() {}
    this._onLongPress = typeof this.props.onLongPress === 'function' ? this.props.onLongPress : function() {}
    this._onTouchStart = typeof this.props.onTouchStart === 'function' ? this.props.onTouchStart : function() {}
    this._onTouchMove = typeof this.props.onTouchMove === 'function' ? this.props.onTouchMove : function() {}
    this._onTouchEnd = typeof this.props.onTouchEnd === 'function' ? this.props.onTouchEnd : function() {}
    this._onClick = typeof this.props.onClick === 'function' ? this.props.onClick : function() {}
  }

  componentDidUpdate() {}

  componentWillUnmount() {}

  handlerClick() {
    let now = Date.now()
    if (now - this.time > 100 && now - this.time < 300) {
      this.time = 0
      this._onDoubleTap()
    } else {
      this.time = now
    }
    this._onClick()
  }

  handlerTouchStart(event) {
    clearTimeout(this._longPressTimerId)
    this._longPressTimerId = setTimeout(() => {
      clearTimeout(this._longPressTimerId)
      this._onLongPress()
    }, this._longPressTime)
    this._onTouchStart(event)
  }

  handlerTouchMove(event) {
    clearTimeout(this._longPressTimerId)
    this._onTouchMove(event)
  }

  handleTouchEnd(event) {
    clearTimeout(this._longPressTimerId)
    this._onTouchEnd(event)
  }

  render() {
    return (
      <div
        className="c-action"
        onClick={() => {
          this.handlerClick()
        }}
        onTouchStart={event => {
          this.handlerTouchStart(event)
        }}
        onTouchMove={event => {
          this.handlerTouchMove(event)
        }}
        onTouchEnd={event => {
          this.handleTouchEnd(event)
        }}
      >
        {this.props.children}
      </div>
    )
  }
}
